.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(#102c78, #09792b);
}

.containerInput {
  display: flex;
  margin-top: 10px;
  outline: none;
  height: 40px;
  width: auto;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 15px;
  margin: 15px;
  border-radius: 9px;
  box-shadow: 1px 3px 8px rgba(0, 0, 0 0.5);
}

.containerInput input {
  background-color: transparent;
  border: 0;
  font-size: 20px;
  outline: none;
  color: white;
  margin-right: 8px;
}

.buttonSearch {
  background-color: transparent;
  border: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.buttonSearch:hover {
  transform: scale(1.5);
}

.container input::placeholder {
  color: white;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgb(223, 223, 223);
  border-radius: 13px;
  margin: 17px;
  width: 500px;
}

.main h2 {
  margin-top: 5px;
  font-size: 26px;
}

.main span {
  margin: 7px;
  font-weight: bold;
}

.title {
  font-size: 85px;
  animation: flipTitle 1.5s;
  color: rgb(223, 223, 223);
}

@media (max-width: 600px) {
  .title {
    font-size: 40px;
  }

  .containerInput {
    height: 20px;
  }
  .main {
    height: auto;
    width: 250px;
  }
}

@keyframes flipTitle {
  from {
    transform: rotateX(90deg);
  }
  to {
    transform: rotateX(0deg);
  }
}
